import {preview} from '../../../common/js/common.js'

export default{
	name:'Banner',
	data(){
		return{
			imgList:[]
		}
	},
	props: {
		detailData: {
			type: Object,
			default(){
				return {}
			}
		},
	},
	watch:{
		detailData(newVal,oldVal){
			this.imgList = newVal.uploadedImg
		}
	},
	methods:{
		onpreview(index){
			preview(index,this.imgList)
		}
	}
}
